* ===================================================
*/
.breadcrumb {
  font-size: 0.875rem;
}

.breadcrumb.text-white .breadcrumb-item {
  color: #fff;
}

.breadcrumb.text-white .breadcrumb-item a {
  color: #fff;
}

.breadcrumb.text-white .breadcrumb-item::before {
  color: #fff;
}

/*
* ===================================================
*     Navbar
* ===================================================
*/
.navbar-brand-svg {
  vertical-align: baseline;
}

.navbar-brand-svg .navbar-brand-svg-text {
  fill: currentColor;
}

.navbar-brand-svg .text-primary {
  fill: theme-colors("primary");
}

.header-absolute {
  position: absolute;
  top: 0;
  right: 0;
  left: 0;
  width: 100%;
  z-index: 1030;
}

.bg-hover-white {
  transition: all 0.2s ease-out;
}

.bg-hover-white:hover {
  background: #fff !important;
}

.navbar.fixed-top.bg-fixed-white {
  background: #fff !important;
}

.navbar {
  /* align with the navbar bottom */
}

.navbar .nav-link {
  font-size: 0.9rem;
  font-weight: bold;
}

.navbar .nav-link:focus {
  outline: none;
}

.navbar .dropdown-toggle::after {
  position: absolute;
  width: 10px;
  height: 10px;
  margin-left: 0.3em;
  content: '\f107';
  vertical-align: top;
  border: none;
  font-family: "Font Awesome\ 5 Free";
  font-weight: 900;
}

.navbar .dropdown-menu {
  min-width: 200px;
  margin-top: 1rem;
  transition: transform .2s;
  transform: translateY(30px);
  color: #333;
  border: none;
  outline: none !important;
  font-size: 0.9rem;
}

.navbar .dropdown-menu::after {
  display: block;
  width: 0;
  height: 0;
  content: '';
  border-right: 7px solid transparent;
  border-bottom: 7px solid #fff;
  border-left: 7px solid transparent;
  position: absolute;
  top: -5px;
  left: 30px;
}

.navbar .dropdown-menu::before {
  display: block;
  width: 0;
  height: 0;
  content: '';
  border-right: 7px solid transparent;
  border-bottom: 8px solid #f1f1f1;
  border-left: 7px solid transparent;
  position: absolute;
  top: -8px;
  left: 30px;
}

.navbar .dropdown-menu.dropdown-menu-end {
  right: 0;
  left: auto;
}

.navbar .dropdown-menu.dropdown-menu-end::after {
  position: absolute;
  top: -5px;
  right: 30px;
  left: auto;
}

.navbar .dropdown-menu.dropdown-menu-end::before {
  position: absolute;
  top: -8px;
  right: 30px;
  left: auto;
}

.navbar .megamenu {
  margin-top: 0px;
}

/*
* ===================================================
*     Megamenu
* ===================================================
*/
.megamenu {
  overflow: hidden;
  width: 100%;
  left: 0;
  top: 100%;
  border: none !important;
  border-radius: 0;
  z-index: 9999;
}

.megamenu .megamenu-list {
  margin-bottom: 1.5rem;
  padding: 0;
  font-size: 0.8rem;
}

.megamenu .megamenu-list-item {
  margin-bottom: 5px;
}

.megamenu .megamenu-list-link {
  padding: 5px 0;
  text-decoration: none;
  color: #6c757d;
}

.megamenu .megamenu-list-link:hover {
  color: #212529;
  background: none;
}

.megamenu .megamenu-services {
  padding-top: 1rem;
  padding-bottom: 1rem;
  background: #f8f9fa;
}

.megamenu .megamenu-services .megamenu-services-item {
  display: flex;
  margin-top: 1rem;
  margin-bottom: 1rem;
  align-items: center;
}

.megamenu .megamenu-services .megamenu-services-icon {
  width: 30px;
  height: 30px;
  margin-right: 1rem;
  flex-shrink: 0;
}

