/*
* ===================================================
*     RIBBONS
* ===================================================
*/
.ribbon {
  position: absolute;
  top: 10px;
  left: -11px;
  z-index: 999;
  display: inline-block;
  font-family: "Poppins", sans-serif;
  font-size: .8rem;
  text-transform: uppercase;
  letter-spacing: .3em;
  padding: 3px 15px 5px;
  color: #fff;
  text-align: center;
}

.ribbon + .ribbon {
  top: 60px;
}

.ribbon + .ribbon + .ribbon {
  top: 110px;
}

.ribbon + .ribbon + .ribbon + .ribbon {
  top: 160px;
}

.ribbon::before {
  content: '';
  border-left: 12px solid transparent;
  border-right: 0 solid transparent;
  border-top: 14px solid #0e5b38;
  position: absolute;
  bottom: -14px;
  left: 0;
}

.ribbon-primary {
  background-color: #1cb36e !important;
}

.ribbon-primary::before {
  border-top-color: #0e5b38;
}

.ribbon-secondary {
  background-color: #48a3e0 !important;
}

.ribbon-secondary::before {
  border-top-color: #1c6fa6;
}

.ribbon-success {
  background-color: #35b653 !important;
}

.ribbon-success::before {
  border-top-color: #1e672f;
}

.ribbon-info {
  background-color: #17a2b8 !important;
}

.ribbon-info::before {
  border-top-color: #0c525d;
}

.ribbon-warning {
  background-color: #ffc107 !important;
}

.ribbon-warning::before {
  border-top-color: #a07800;
}

.ribbon-danger {
  background-color: #dc3545 !important;
}

.ribbon-danger::before {
  border-top-color: #921925;
}

.ribbon-light {
  background-color: #f8f9fa !important;
}

.ribbon-light::before {
  border-top-color: #bdc6d0;
}

.ribbon-dark {
  background-color: #343a40 !important;
}

.ribbon-dark::before {
  border-top-color: #060708;
}

